// @flow

import Head from 'next/head';

type Props = {
    title?: string,
    description?: string,
    author?: string
};

const prefixes = ['', 'dc:', 'og:', 'twitter:'];

export default function Seo({title, description, author}: Props) {
    return (
        <Head>
            {title && (
                <>
                    <title>{title}</title>
                    <meta name="dc:title" content={title} />
                </>
            )}
            {author && (
                <>
                    <meta name="author" content={author} />
                    <meta name="dc:creator" content={author} />
                </>
            )}
            {description &&
                prefixes.map(prefix => (
                    <meta
                        key={prefix}
                        name={`${prefix}description`}
                        content={description}
                    />
                ))}
        </Head>
    );
}
